import React, { useContext } from "react";
import "./aboutpage.styles.scss";
import { DataContext } from "../../contexts/DataProvider.js";

import TitleH1 from "../../components/title_h1/titleH1.component";
import Section from "../../components/section/section.component";
import Hero from "../../components/hero/hero.component.jsx";

function AboutPage() {
	const { sectionData, hero } = useContext(DataContext);
	return (
		<>
			{hero.map((obj, i) => {
				return obj.page === "about" ? (
					<Hero key={i} title={obj.heroTitle} bibleVerse={obj.bibleVerse} src={obj.image} alt={obj.headline} />
				) : null;
			})}
			<div className='aboutpageContainer'>
				{sectionData.map((obj, i) => {
					return obj.page === "about" ? (
						<Section
							key={i}
							headline={obj.headline}
							p1={obj.p1}
							{...(obj.p2 && { p2: obj.p2 })}
							src={obj.image}
							alt={obj.headline}
							to={obj.to}
							buttonLabel={obj.buttonLabel}
							cName={obj.cName}
							textColor={obj.h1TextColor}
						/>
					) : null;
				})}

				<div className='historyContainer'>
					<div className='border'>
						<div className='centerTitle'>
							<TitleH1 title='A Gylekezetnk trtnete' />
							<div className='underline'></div>
						</div>

						<div className='historyContent'>
							<div className='indentation'>
								Gylekezetnk a 12 kolozsvri reformtus gylekezet egyike, a vros szak-keleti rszn.
							</div>
							<br />
							<div className='indentation'>
								Gylekezetnk 1977 jlius 1-n nllsult, amikor az jalsvrosi , azaz V. sz. gylekezettl elvlva,
								kln gylekezett vlt. Ez a gylekezet-rsz is, akrcsak az jalsvrosi, korbban az alsvrosi
								egyhzkzsghez tartoz klvrosi terleteken szervezdtt. A vros gyors tem kiterjedse, s ezzel
								egytt a llekszm nvekedse tette ezt szksgess.
							</div>
							<br />
							<div className='indentation'>
								Az alsvrosi gylekezetnek a Bulgriatelepen mr a 20. szzad els felben is volt hol itt, hol ott
								ideiglenesen berendezett istentiszteleti otthona, ahol az alsvrosi lelkipsztorok rendszeresen
								szolgltak. Ezeken az alkalmakon fleg azok az ids hstti atyafiak vettek rszt, akik a tvoli
								ktg templomba nehezebben jutottak volna el.
							</div>
							<br />
							<div className='indentation'>
								A Bulgriatelepen a mlt szzad hetvenes veiben sikerlt egy magn hzat megvsrolni a Boblna u. 24.
								szm alatt s gy lland gylekezeti hajlkot kialaktani. Ebben az imahzban rendszeresen szolglt az
								jalsvrosi egyhzkzsg segdlelkszeknt 1971-tol Szcs Lszl, akit a gylekezet az nllsulsa
								alkalmval vglegesen lelkipsztornak vlasztott 1977 jlius 1.-n.
							</div>
							<br />
							<div className='indentation'>
								A gyors temben pl krnyk tmbhzastsa a nyolcvanas vekben is folytatdott. Ennek nyomn a
								gylekezet sszllekszma a rendszervlts idejre mr 2100 llekre emelkedett. A rendszervltst
								kveten, a gylekezet egyhang meghvs alapjn az addig Visban szolgl Visky Jnost vlasztotta
								lelkipsztornak, aki 1990 janur 1. ta szolgl a gylekezetben.
							</div>
							<div className='indentation'>
								A megvltozott trsadalmi-politikai helyzetben azonnal lehetsg nylt egy j gylekezeti kzpont
								ptsre, aminek szksgt mindenki rezte, kivitelezst viszont az anyagiak hinya miatt sokan nem
								lttk lehetsgesnek. Isten azonban nem csak a babiloni fogsgbl szabadult npnek adta ki egykor a
								templom ptsre vonatkoz parancsot, hanem neknk is: ptstek e hzat! Biztatst is adott hozz az
								Esdrs 6,4 ltal: ...a kltsg a kirly hzbl adassk! Nem vletlen, hogy ugyanezen Ige alapjn
								szlt az alapkletteli nnepsgen 1991 jnius 30.-n a Dr. Csiha Klmn pspk r igehirdetse.
							</div>
							<br />
							<div className='indentation'>
								A lelki terror sttsge utn a szellemi-lelki let romjain j letnek kellett kibontakoznia. Ennek
								beszdes bizonytka az 1991-95 kztt megplt gylekezeti kzpont, amely teljes mrtkben megfelel a
								21. szzad komplex lelki-kzssgi ignyeinek. Az pletegyttes 400 frhelyes templomot, lelkszi,
								segdlelkszi lakst, ifjsgi termet, ovodt, gylekezeti konyht s vendg-szobkat foglal magba. Az
								pts ideje alatt a falak plsvel prhuzamosan ntt a mindensg gazdag Urba vetett bizalmunk s
								hitnk, aki lpsrl-lpsre kirendelte a hatalmas munkhoz szksges anyagiakat a gylekezeti tagok s
								klfldi testvrek ldozatbl. Az avat nnepsg 1995 jnius 15.-n zajlott ugyancsak Dr. Csiha Klmn
								pspk r igehirdetsvel.
							</div>
							<br />
							<div className='indentation'>
								Az pletegyttes ftervezje Zakaris Attila, sepsiszentgyrgyi mrnk volt, aki harmonikusan tvzte
								az erdlyi hagyomnyos s a modern ptszeti vonalakat. gy szletett meg egy otthonos, bels s kls
								megjelensben vonz pletegyttes mindnyjunk rmre. Ezzel prhuzamosan s azta is a legfontosabb
								feladatunk a gylekezet bels, lelki ptse. A fordulat ta eltelt 18 v llekszmbeli lemorzsoldst
								hozott. Ennek oka egyrszt a kivndorls, msrszt az asszimilci, valamint a szletsek szmnak
								drasztikus cskkense. Gylekezetnk jelenlegi llekszma 1700 krl mozog.
							</div>
							<br />
							<div className='indentation'>
								Terletileg 28 presbiteri krzetre oszlik. A krzeti felelsk munkja nyomn tlthat a gylekezet
								lete. A lelki munka tern nagy hangslyt fektetnk a csaldltogatsok rendjn kialaktand szemlyes
								kapcsolatokra, az ifjsgi munkra, a rendszeresen szervezett gylekezeti hitmlyt kzssgi
								alkalmakra, htvgekre s nyri tborokra, melyeket kln szerveznk a felntteknek s kln az
								ifjsgnak. Hitbreszt evangelizcis heteket is szerveznk, npnk lelki bredsnek cljbl. Valljuk
								ugyanis, hogy az a np, amely nem ismeri Istent, elvsz. Ezrt tartjuk roppant fontosnak, hogy a
								Krisztussal val szemlyes kapcsolat nyomn Istennel s embertrsaival megbklt gylekezeti tagok
								sokasga zrkozzon fel reformtus anyaszentegyhzunk kzssgbe.
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
}

export default AboutPage;
